import { NgModule } from '@angular/core';
import { ROUTES } from '@angular/router';
import { WebDynamicRoutingService } from '../../../projects/app-core-lib/src/lib/@services/web-dynamic-routing.service';


@NgModule({
  imports: [],
  providers: [
    {
      provide: ROUTES,
      useFactory: (webDynamicRoutingService: WebDynamicRoutingService) =>
      {
        return webDynamicRoutingService.dynamicChildRoutes;
      },
      deps: [WebDynamicRoutingService],
      multi: true,
    }
  ],
})
export class WebModule
{

}